/*variables*/
@import "./var.scss";
/*normalize css https://necolas.github.io/normalize.css/ */
@import "vendor/normalize.scss";
/*main style*/
html{
  font-size: 16px;
}
body{
  font-family: sans-serif;
  font-size: 1rem;
  -webkit-font-smoothing: antialiased;
  color: $black;
  margin: 0;
  padding: 0;
  background-color: $white;
  overflow-x: hidden;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}
p {
  margin: 0;
}
a {
  &:focus {
    outline: none;
  }
}
.alert{
  background-attachment: fixed;
}
/*helpers class*/
@import "./help.scss";
/*mixins*/
@import "./mixin.scss";
/*vendor*/
/*slick*/
@import "vendor/slick.scss";
/*lightbox*/
@import "vendor/lightbox.scss";
/*waves*/
@import "vendor/waves.scss";
/*block*/
/*container*/
